.App {
  max-width: 1024px;
  margin: 0 auto;
  height: 100vh;
  transition: 0.2s opacity ease-in-out;
}

.loading {
  opacity: 0;
}

.emojo {
  text-align: center;
  font-size: 5rem;
}

.heading {
  padding: 20vh 24px;
  text-align: center;
}

.title {
  font-size: 4.5rem;
  font-weight: bold;
}

.illus {
  left: 50%;
  transform: translateX(-50%);
}

.emojocontrol {
  margin-top: 10vh;
}

.positi {
  padding-top: 10vh;
}
